"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { AlertCircle, CheckCircle, HelpCircle, Send } from "lucide-react"

export function SupportSection() {
    const [loading, setLoading] = useState(false)
    const [success, setSuccess] = useState(false)
    const [error, setError] = useState<string | null>(null)

    async function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
        e.preventDefault()
        setLoading(true)
        setError(null)
        setSuccess(false)

        const formData = new FormData(e.currentTarget)
        const data = {
            name: formData.get("name"),
            email: formData.get("email"),
            subject: formData.get("subject"),
            message: formData.get("message"),
        }

        try {
            const res = await fetch("/api/support", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(data),
            })

            if (!res.ok) throw new Error("Failed to submit support request")

            setSuccess(true)
            // Reset form
            e.currentTarget.reset()
        } catch (err) {
            setError("Something went wrong. Please try again.")
        } finally {
            setLoading(false)
        }
    }

    return (
        <Card className="border-primary/20 shadow-md">
            <CardHeader>
                <div className="flex items-center gap-2">
                    <HelpCircle className="h-6 w-6 text-primary" />
                    <CardTitle>Contact Support</CardTitle>
                </div>
                <CardDescription>
                    Having trouble? Send us a message and we'll help you right away.
                </CardDescription>
            </CardHeader>
            <CardContent>
                {success ? (
                    <div className="flex flex-col items-center justify-center p-6 text-center space-y-4 bg-green-50 text-green-800 rounded-lg">
                        <CheckCircle className="h-12 w-12 text-green-600" />
                        <div>
                            <h3 className="font-semibold text-lg">Request Sent!</h3>
                            <p>We have received your message and will get back to you shortly.</p>
                        </div>
                        <Button variant="outline" onClick={() => setSuccess(false)}>
                            Send another message
                        </Button>
                    </div>
                ) : (
                    <form onSubmit={handleSubmit} className="space-y-4">
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="space-y-2">
                                <label htmlFor="name" className="text-sm font-medium">
                                    Name
                                </label>
                                <Input id="name" name="name" placeholder="Your name" required />
                            </div>
                            <div className="space-y-2">
                                <label htmlFor="email" className="text-sm font-medium">
                                    Email
                                </label>
                                <Input id="email" name="email" type="email" placeholder="your@email.com" required />
                            </div>
                        </div>
                        <div className="space-y-2">
                            <label htmlFor="subject" className="text-sm font-medium">
                                Subject
                            </label>
                            <Input id="subject" name="subject" placeholder="What can we help with?" required />
                        </div>
                        <div className="space-y-2">
                            <label htmlFor="message" className="text-sm font-medium">
                                Message
                            </label>
                            <Textarea
                                id="message"
                                name="message"
                                placeholder="Describe your issue..."
                                rows={4}
                                required
                            />
                        </div>

                        {error && (
                            <div className="flex items-center gap-2 text-red-600 bg-red-50 p-3 rounded-md text-sm">
                                <AlertCircle className="h-4 w-4" />
                                {error}
                            </div>
                        )}

                        <Button type="submit" className="w-full md:w-auto" disabled={loading}>
                            {loading ? (
                                "Sending..."
                            ) : (
                                <>
                                    <Send className="mr-2 h-4 w-4" /> Send Message
                                </>
                            )}
                        </Button>
                    </form>
                )}
            </CardContent>
        </Card>
    )
}
