"use client"
// Force rebuild


import { useEffect, useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Calendar, MapPin, Clock } from "lucide-react"
import { format } from "date-fns"

interface Booking {
    id: string
    activityTitle: string
    date: string
    status: string
    totalPrice: number
    guests: number
    pickupLocation?: string
    imageUrl?: string | null
}

export function MyBookings() {
    const [bookings, setBookings] = useState<Booking[]>([])
    const [loading, setLoading] = useState(true)
    const [activeTab, setActiveTab] = useState<'upcoming' | 'past'>('upcoming')

    useEffect(() => {
        fetch("/api/my-bookings")
            .then((res) => res.json())
            .then((data) => {
                if (Array.isArray(data)) {
                    setBookings(data)
                }
            })
            .catch((err) => console.error("Failed to fetch bookings", err))
            .finally(() => setLoading(false))
    }, [])

    const upcomingBookings = bookings.filter(
        (b) => new Date(b.date) >= new Date() && b.status !== "CANCELLED"
    )
    const pastBookings = bookings.filter(
        (b) => new Date(b.date) < new Date() || b.status === "CANCELLED"
    )

    if (loading) {
        return <div className="text-center py-8">Loading bookings...</div>
    }

    return (
        <Card className="h-full">
            <CardHeader>
                <CardTitle>My Bookings</CardTitle>
                <CardDescription>Manage your upcoming trips and view past history</CardDescription>
            </CardHeader>
            <CardContent>
                <div className="w-full">
                    <div className="flex gap-2 mb-4 border-b">
                        <button
                            onClick={() => setActiveTab('upcoming')}
                            className={`px-4 py-2 text-sm font-medium border-b-2 transition-colors ${activeTab === 'upcoming'
                                ? 'border-primary text-primary'
                                : 'border-transparent text-muted-foreground hover:text-foreground'
                                }`}
                        >
                            Upcoming ({upcomingBookings.length})
                        </button>
                        <button
                            onClick={() => setActiveTab('past')}
                            className={`px-4 py-2 text-sm font-medium border-b-2 transition-colors ${activeTab === 'past'
                                ? 'border-primary text-primary'
                                : 'border-transparent text-muted-foreground hover:text-foreground'
                                }`}
                        >
                            Past / Cancelled
                        </button>
                    </div>

                    {activeTab === 'upcoming' && (
                        <div className="space-y-4 animate-in fade-in slide-in-from-top-2">
                            {upcomingBookings.length === 0 ? (
                                <p className="text-muted-foreground text-center py-8">No upcoming bookings found.</p>
                            ) : (
                                upcomingBookings.map((booking) => (
                                    <BookingItem key={booking.id} booking={booking} />
                                ))
                            )}
                        </div>
                    )}

                    {activeTab === 'past' && (
                        <div className="space-y-4 animate-in fade-in slide-in-from-top-2">
                            {pastBookings.length === 0 ? (
                                <p className="text-muted-foreground text-center py-8">No past bookings found.</p>
                            ) : (
                                pastBookings.map((booking) => (
                                    <BookingItem key={booking.id} booking={booking} />
                                ))
                            )}
                        </div>
                    )}
                </div>
            </CardContent>
        </Card>
    )
}

function BookingItem({ booking }: { booking: Booking }) {
    const statusColor =
        booking.status === "CONFIRMED" ? "bg-green-100 text-green-800" :
            booking.status === "PENDING" ? "bg-yellow-100 text-yellow-800" :
                "bg-red-100 text-red-800"

    return (
        <div className="border rounded-lg p-4 flex flex-col md:flex-row items-start gap-4 bg-card hover:bg-accent/10 transition-colors">

            {/* Image Thumbnail */}
            <div className="relative h-24 w-24 md:h-32 md:w-32 flex-shrink-0 rounded-md overflow-hidden bg-muted">
                {booking.imageUrl ? (
                    <img
                        src={booking.imageUrl}
                        alt={booking.activityTitle}
                        className="object-cover w-full h-full"
                    />
                ) : (
                    <div className="flex items-center justify-center h-full text-muted-foreground">
                        <Calendar className="h-8 w-8 opacity-50" />
                    </div>
                )}
            </div>

            <div className="flex-grow space-y-1 w-full">
                <div className="flex justify-between items-start">
                    <h4 className="font-semibold text-lg">{booking.activityTitle}</h4>
                    <Badge variant="secondary" className={statusColor}>
                        {booking.status}
                    </Badge>
                </div>

                <div className="flex flex-wrap gap-x-4 gap-y-2 text-sm text-muted-foreground mt-2">
                    <div className="flex items-center gap-1">
                        <Calendar className="h-4 w-4" />
                        {format(new Date(booking.date), "PPP")}
                    </div>
                    <div className="flex items-center gap-1">
                        <Clock className="h-4 w-4" />
                        {booking.guests} Guests
                    </div>
                    {booking.pickupLocation && (
                        <div className="flex items-center gap-1">
                            <MapPin className="h-4 w-4" />
                            {booking.pickupLocation}
                        </div>
                    )}
                </div>

                <div className="pt-2 flex justify-end">
                    <span className="font-bold text-lg">${booking.totalPrice}</span>
                </div>
            </div>
        </div>
    )
}
